<template>
    <div class="uk-animation-slide-bottom-medium">  
        <div class="uk-width-2-3 uk-align-center uk-align-center uk-margin-remove-top">  
            <div class="uk-margin">
                <div class="uk-card uk-card-small uk-card-default uk-card-body">
                    <img src="assets/head.gif" class="uk-align-left" width="60">
                    <div class="uk-text-justify">{{ getAboutText() }}</div>
                </div>
            </div>
            
            <div v-if="data.achievements.awards" class="uk-margin">
                <div class="uk-card uk-card-small uk-card-default uk-card-body">
                    <div class="uk-flex uk-flex-center">
                        <div class="uk-grid-medium uk-grid-divider" uk-grid>
                            <div v-if="data.achievements.awards" class="uk-width-auto">
                                <div class="uk-grid-collapse" uk-grid>
                                    <div v-for="(award, index) in data.achievements.awards" :key="index" :uk-tooltip="award.text">
                                        <img src="assets/images/awards/leaf_L.png" width="25">
                                        <img :src="award.img" width="70">
                                        <img src="assets/images/awards/leaf_R.png" width="25">
                                    </div>
                                </div>
                            </div>

                            <div v-if="data.achievements.nominations" class="uk-width-expand">
                                <div class="uk-grid-collapse" uk-grid>
                                    <div 
                                        v-for="(nomination, index) in data.achievements.nominations" 
                                        :key="index"
                                        :uk-tooltip="nomination.text">
                                        <img :src="nomination.img" width="50">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="uk-margin">
                <div class="uk-card uk-card-small uk-card-default uk-card-body">
                    <div class="uk-text-meta">Experiences:</div>
                    <div class="uk-grid-small uk-child-width-1-1@s uk-child-width-1-2@m uk-child-width-1-3@l" uk-grid>
                        <div 
                            v-for="(item, index) in data.experiences"
                            :key="index">
                            <div class="uk-grid-small" uk-grid>
                                <div class="uk-width-auto">
                                    <img :src="item.logo" width="64">
                                </div>
                                <div class="uk-with-expand">
                                    <span class="uk-text-bold">{{ item.title }}</span><br>
                                    <span>{{ item.company }}</span><br>
                                    <span>{{ item.duration }}</span><br>
                                    <span>{{ item.location }}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="uk-margin">
                <div class="uk-card uk-card-small uk-card-default uk-card-body">
                    <div class="uk-text-meta">Certificates:</div>
                    <div class="uk-grid-small uk-child-width-1-1@s uk-child-width-1-2@m uk-child-width-1-3@l" uk-grid>
                        <div 
                            v-for="(item, index) in data.certificates"
                            :key="index">
                            <div class="uk-grid-small" uk-grid>
                                <div class="uk-width-auto">
                                    <img :src="item.logo" width="64">	
                                </div>
                                <div class="uk-width-expand">
                                    <span class="uk-text-bold">{{ item.title }}</span><br>
                                    <span>{{ item.company }}</span><br>
                                    <span>{{ item.duration }}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="uk-margin">
                <div class="uk-card uk-card-small uk-card-default uk-card-body">
                    <div class="uk-text-meta">Skillsets:</div>
                    <div uk-grid class="uk-grid-collapse">
                        <div 
                            v-for="(item, index) in data.skillset"
                            :key="index">
                            <button 
                                v-if="item.primary == 1" 
                                class="uk-button uk-button-default">
                                {{ item.name }}
                            </button>
                        </div>
                    </div>
                    <div class="uk-margin">
                        <div uk-grid class="uk-grid-collapse">
                            <div 
                                v-for="(item, index) in data.skillset"
                                :key="index">
                                <button 
                                    v-if="item.primary == 0"
                                    class="uk-button uk-button-default uk-button-small">
                                    {{ item.name }}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// import Awards from './components/Awards.vue'
import Data from '@/assets/data/about.json'

export default {
    // components:{
    //     Awards
    // },
    data(){
        return {
            data: Data
        }
    },

    methods: {
        getAboutText(){
            var age = this.getAge();
            var aboutText = 
                this.data.aboutText
                    .replace('[AGE]', age);

            return aboutText;
        },

        getAge(){
            var today = new Date();
            var birthDate = new Date('1991/03/20');
            var age = today.getFullYear() - birthDate.getFullYear();
            var m = today.getMonth() - birthDate.getMonth();
            if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
                age--;
            }
            return age;
        }
    }
}
</script>